import React, { useContext } from "react";
import DataTable from "react-data-table-component";
import Card from "@material-ui/core/Card";
import "./Tables.css";

import { MyContext } from "../../../../Context/MyProvider";
import { styles } from "../helpers";


const columns = [
    {
        name: " ",
        selector: "0",
        // grow: ,
        wrap: true,
        // width:'35px',
    },
    {
        name: "20-2",
        selector: "20 2",
        // grow: 0.05,
        // width:'110px',
        wrap: true,
        format: data => data["20 2"].toLocaleString('en-IN')
    },
    {
        name: "202-2",
        selector: "202 2",
        // grow: 0.05,
        // width:'110px',
        wrap: true,
        format: data => data["202 2"].toLocaleString('en-IN')
    },
    {
        name: " (+)/  (-)",
        selector: "3",
        // grow: 0.05,
        // width:'110px',
        wrap: true,
        format: data => data["3"].toLocaleString('en-IN')
    },
    {
        name: " ",
        selector: "4",
        // grow:0.05,
        wrap: true,
        format: data => data["4"].toLocaleString('en-IN')
    }
];


const customStyles = styles;
const conditionalRowStyles = [
    {
        when: row => row["0"] === ' ',
        style: {
            backgroundColor: 'rgba(153, 165, 128, 0.9)',
        },
    }
  ]


const Table20dkan = () => {

    const ctx = useContext(MyContext)

    return (
        <div className="App" style={{ margin: "40px 0 40px 0" }} >
            <Card>
                <DataTable
                    title="-2.2: 20-2  202-2  "
                    columns={columns}
                    data={ctx.tables2kan.Table20d}
                    customStyles={customStyles}
                    striped
                    conditionalRowStyles={conditionalRowStyles}
                    highlightOnHover
                    pointerOnHover
                    // pagination
                />
            </Card>
        </div>
    );
}

export default Table20dkan;
