import React, { useContext } from "react";
import DataTable from "react-data-table-component";
import Card from "@material-ui/core/Card";
import "./Tables.css";

import { MyContext } from "../../../../Context/MyProvider";
import { styles } from "../helpers";


const columns = [
    {
        name: " ",
        selector: " ",
        grow: 2,
        wrap: true
    },
    {
        name: " ",
        selector: "1",
        wrap: true,
    },
    {
        name: " / ",
        selector: " / ",
        // grow:0.05,
        wrap: true,
        
    },
    {
        name: " / ",
        selector: " / ",
        // grow:0.05,
        wrap: true,
       
    },
    {
        name: "",
        selector: "",
        // grow:0.05,
        wrap: true,
       
    },
    {
        name: " ",
        selector: " ",
        // grow:0.05,
        wrap: true,
      
    },
    {
        name: "   (-) /   (+)",
        selector: "   (-) /   (+)",
        // grow:0.05,
        wrap: true,
      
    },
    {
        name: " ",
        selector: " ",
        // grow:0.05,
        wrap: true,
        
    },
    {
        name: "    ",
        selector: "    ",
        // grow:0.05,
        wrap: true,
       
    },
    {
        name: "    ",
        selector: "    ",
        // grow:0.05,
        wrap: true,
        
    }
];

const customStyles = styles;

const Table5kan = () => {

    const ctx = useContext(MyContext)

    return (
        <div className="App" style={{ margin: "40px 0 40px 0" }} >
            <Card>
                <DataTable
                    title="-.: - /     "
                    columns={columns}
                    data={ctx.tables3kan.Table5}
                    customStyles={customStyles}
                    striped
                    // conditionalRowStyles={conditionalRowStyles}
                    highlightOnHover
                    pointerOnHover
                    // pagination
                />
            </Card>
        </div>
    );
}

export default Table5kan;