import React, { useContext } from "react";
import DataTable from "react-data-table-component";
import Card from "@material-ui/core/Card";
import "./Tables.css";

import { MyContext } from "../../../../Context/MyProvider";
import { styles } from "../helpers";


const columns = [
  {
    name: "  ",
    selector: "  ",
    grow: 3,
    wrap: true,
  },
  {
    name: "20 2",
    selector: "20 2",
    wrap: true,
    format: data => data["20 2"].toLocaleString('en-IN')
  },
  {
    name: "202 2",
    selector: "202 2",
    wrap: true,
    format: data => data["202 2"].toLocaleString('en-IN')
  },
  {
    name: " (+)/  (-)",
    selector: " (+)/  (-)",
    wrap: true,
    format: data => data[" (+)/  (-)"].toLocaleString('en-IN')
  },
  {
    name: "",
    selector: "",
    wrap: true,
    format: data => data[""].toLocaleString('en-IN')
  }
 

];


const customStyles = styles;


const Table15kan = () => {

  const ctx = useContext(MyContext)

  return (
    <div className="App" style={{ margin: "40px 0 40px 0" }} >
      <Card>
        <DataTable
          title="-2.1: 20-2  202-2   "
          columns={columns}
          data={ctx.tables2kan.Table14}
          customStyles={customStyles}
          striped
          // conditionalRowStyles={conditionalRowStyles}
          highlightOnHover
          pointerOnHover
        // pagination
        />
      </Card>
    </div>
  );
}

export default Table15kan;
