import React, { useContext } from "react";
import DataTable from "react-data-table-component";
import Card from "@material-ui/core/Card";
import "./Tables.css";

import { MyContext } from "../../../../Context/MyProvider";
import { styles } from "../helpers";


const columns = [
    {
        name: "  ",
        selector: "1",
        // grow: ,
        wrap: true,
        // width:'35px',
    },
    {
        name:"   ",
        selector:"   ",
        // grow: 0.05,
        // width:'110px',
        wrap: true,
        sortable:true,
        format: data => data["   "].toLocaleString('en-IN')
    },
    {
        name: " ",
        selector: " ",
        // grow: 0.05,
        // width:'110px',
        wrap: true,
        format: data => data[" "].toLocaleString('en-IN')
    },
    {
        name: "",
        selector: "",
        // grow: 0.05,
        // width:'110px',
        wrap: true,
        format: data => data[""].toLocaleString('en-IN')
    },
    {
        name: "   ",
        selector: "   ",
        // grow: 0.05,
        // width:'110px',
        wrap: true,
        format: data => data["   "].toLocaleString('en-IN')
    },
    {
        name: "",
        selector: "2",
        // grow: 0.05,
        // width:'110px',
        wrap: true,
        format: data => data["2"].toLocaleString('en-IN')
    }
];


const customStyles = styles;

const Table11kan = () => {

    const ctx = useContext(MyContext)

    return (
        <div className="App" style={{ margin: "40px 0 40px 0" }} >
            <Card>
                <DataTable
                    title="-2.1: 202-2      "
                    columns={columns}
                    data={ctx.tables2kan.Table11}
                    customStyles={customStyles}
                    striped
                    // conditionalRowStyles={conditionalRowStyles}
                    highlightOnHover
                    pointerOnHover
                    // pagination
                />
            </Card>
        </div>
    );
}

export default Table11kan;
